.input::-webkit-outer-spin-button,
.input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.input[type=number] {
  appearance: textfield;
}

.input {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  display: flex;
  padding: 13px 20px;
  border: 1px solid #e7e7e7;
  flex-grow: 1;
  border-radius: 0;
}
.input input[type=number]::-webkit-outer-spin-button,
.input input[type=number]::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}
@media (min-width: 768px) {
  .input {
    padding: 10px 20px;
    font-size: 20px;
    line-height: 30px;
  }
}
.input::placeholder {
  color: #444444;
}
.input:hover {
  border-color: #cdcdcd;
}
.input:focus {
  outline: 2px solid #444444;
}
.input:focus-visible {
  outline: none;
}
.input:invalid:not(:placeholder-shown) {
  outline: 2px solid #ff8282;
}
.input:disabled {
  opacity: 30%;
}

.input--age {
  width: 10px;
}

.input--email {
  border-color: #cdcdcd;
}
.input--email:hover {
  border-color: #e7e7e7;
}
.input--email:focus ~ .input--email__image {
  color: #68b738;
}
.input--email:invalid:not(:placeholder-shown) ~ .input--email__image {
  color: #ff8282;
}

.input--email__image {
  position: absolute;
  content: "";
  right: 14px;
}
@media (min-width: 768px) {
  .input--email__image {
    right: 20px;
  }
}

.input--tel {
  border-color: #cdcdcd;
}
.input--tel:hover {
  border-color: #e7e7e7;
}
.input--tel:focus ~ .input--tel__image {
  color: #68b738;
}
.input--tel:invalid:not(:placeholder-shown) ~ .input--tel__image {
