.about {
  position: relative;
  font-family: "Oswald", sans-serif;
  color: #ffffff;
  text-align: center;
  background: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 60%, #ffffff 60%);
  padding-bottom: 20px;
  transition: 0.5s;
}
@media (min-width: 768px) {
  .about {
    background: inherit;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .about {
    background: linear-gradient(to right, #ffffff 50%, rgba(104, 183, 56, 0.85) 50%);
    padding-top: 141px;
    padding-bottom: 2px;
  }
}

.about__container {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  min-height: 475px;
}
@media (min-width: 768px) {
  .about__container {
    margin: 0 auto;
    align-items: flex-start;
  }
}
.about__container::after {
  display: block;
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-image: url("../../img/index/about-back-image.png");
  background-repeat: no-repeat;
  z-index: -1;
}
@media (min-resolution: 2dppx) {
  .about__container::after {
    background-image: url("../../img/index/about-back-image@2x.png");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .about__container::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .about__container::after {
    display: block;
    background-image: url("../../img/index/about-back-image-large.png");
    width: 50%;
    height: 694px;
    background-repeat: no-repeat;
    top: -141px;
    right: 0;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .about__container::after {
    background-image: url("../../img/index/about-back-image-large@2x.png");
    background-size: cover;
  }
}

